import React from 'react';
import classes from "./Users.module.css"
import aki from '../../assets/images/aki.jpg'
import { NavLink } from 'react-router-dom';


let User = ({user,followingInProgress,follow,unfollow,...props}) => {
    return (
            <div className={classes.user} key={user.id}>
                    <div>
                        <div>
                            <NavLink to={'/profile/' + user.id}>
                                <img className = {classes.ava} src={user.photos.small != null ? user.photos.small:aki} alt="photoAva" />
                            </NavLink>
                        </div>
                        <div>
                            {user.followed 
                            ? <button disabled={followingInProgress.some(id => id === user.id)} className={classes.unfollow} onClick={()=> {
                                unfollow(user.id)}}>Unfollow</button>
                            :<button disabled={followingInProgress.some(id => id === user.id)} className={classes.follow} onClick={()=> {
                                follow(user.id)}}> Follow</button>}
                        </div>
                    </div>
                    <span>
                        <span>
                            <div>Name: {user.name}</div>
                            <div>Status: '{user.status}'</div>
                        </span>
                        <span>
                            <div>{"u.location.country"}</div>
                            <div>{"u.location.city"}</div>
                        </span>
                    </span>
                </div>
    );
}
export default User;
