import { profileAPI, usersAPI } from "../api/api";

const ADD_POST = 'vk/profile/ADD-POST';
const UPDATE_NEW_POST_TEXT= 'vk/profile/UPDATE-NEW-POST-TEXT';
const SET_USER_PROFILE= 'vk/profile/SET_USER_PROFILE';
const SET_STATUS= 'vk/profile/SET_STATUS';
const DELETE_POST='vk/profile/DELETE_POST'

let initialState = {
        posts:[
            { id: 1, message: "еще и хомяк:(((", likeCount: 12 },
            { id: 2, message: "У  меня сдох кот", likeCount: 234 },
            { id: 3, message: "Подписчики привееееееет", likeCount: 14 },
        ],
        profile: null,
        status:""
    }

const profileReducer = (state = initialState, action) => {
    switch(action.type) {
        case ADD_POST: {

            return {
                ...state,
                newPostText:'',
                posts:[{id:4,message: action.newPostText,likeCount:0},...state.posts]
            };
        }
        case DELETE_POST: {

            return {
                ...state,
                posts:[...state.posts.filter(p => p.id !== action.postId)]
            };
        }
        case SET_USER_PROFILE: {
            return {
                ...state,
                profile: action.profile
            };
        }
        case SET_STATUS: {
            return {
                ...state,
                status: action.status
            };
        }
        default:
            return state;
    }  
}
export default profileReducer;
export const addPostActionCreator = (newPostText) => ({type: ADD_POST,newPostText})
export const setStatus = (status) => ({type: SET_STATUS,status})
export const deletePost = (postId) => ({type: DELETE_POST,postId})
export const updateNewPostTextActionCreator = (text) => 
    ({type: UPDATE_NEW_POST_TEXT, newText:text})
export const setUserProfile = (profile) => ({type: SET_USER_PROFILE,profile})
export const getUserProfile = (userId) => async (dispatch)=> {
    let response = await usersAPI.getProfile(userId) 
    dispatch(setUserProfile(response.data));
}
export const getStatus = (userId) => async (dispatch)=> {
    let response = await profileAPI.getStatus(userId)
        dispatch(setStatus(response.data));
}
export const updateStatus = (status) => async (dispatch)=> {
    let response = await profileAPI.updateStatus(status) 
    if (response.data.resultCode === 0){
        dispatch(setStatus(status));
    }
}

