import { NavLink } from 'react-router-dom';
import classes from './Header.module.css'
const Header =(props)=>{
    return (
        <header className={classes.header}>
            <img className={classes.logo} src='https://upload.wikimedia.org/wikipedia/commons/3/36/Vkontakte.png' alt='header' />

            <div className={classes.loginBlock}>
                {props.isAuth 
                ? <div>
                    <NavLink to={'/profile'}>{props.login}</NavLink> - <NavLink to={'/login'}><button onClick={props.logout}>Log out</button></NavLink>
                </div> 
                :  <NavLink to={'/login'}>Login</NavLink>}
            </div>
        </header>
    );
}

export default Header;