import './App.css';
import Nav from './componets/Nav/Nav';
import {BrowserRouter, Route, Routes} from 'react-router-dom';
import UsersContainer from './componets/Users/UsersContainer';
import ProfileContainer from './componets/Profile/ProfileContainer';
// import NewsContainer from './componets/News/NewsContainer';
import HeaderContainer from './componets/Header/HeaderContainer';
// import DialogsContainer from './componets/Dialogs/DialogsContainer';
import LoginPage from './componets/Login/Login';
import { Component } from 'react';
import { connect } from 'react-redux';
import { initializeApp } from './redux/app-reducer';
import Preloader from './componets/common/Preloader/Preloader';
import store from "./redux/redux-store";
import { Provider } from "react-redux";
import React, {Suspense, lazy} from 'react';

const DialogsContainer = lazy(() => import("./componets/Dialogs/DialogsContainer"));
const NewsContainer = lazy(() => import("./componets/News/NewsContainer"));

class App extends Component {
  componentDidMount(){
    this.props.initializeApp();
  }
  render() {
    if (!this.props.initialized){
      return <Preloader/>
    }
    return (
      <BrowserRouter>
      <div className="app-wrapper">
        <HeaderContainer />
        <Nav />
        <div className="app-wrapper-content">
        <Suspense fallback={<div>Loading...</div>}>
            <Routes>
              <Route exact path="/dialogs" element={
              <DialogsContainer 
              />} />
              {/* <Route path="/profile" element={ <ProfileContainer  />} /> */}
              <Route exact path='profile/:userId?' element={<ProfileContainer/>} />
              <Route path="/users" element={ <UsersContainer/>} />
              <Route path="/news" element={<NewsContainer />} />
              <Route path="/login" element={<LoginPage/>} />
              {/* <Route path="/" element={<News state = {props.state.newsPage}/>} /> */}
            </Routes>
          </Suspense>
        </div>
      </div>
      </BrowserRouter>
    );
  }
}

// state = {props.state.newsPage} dispatch={props.dispatch}
const mapStateToProps = (state) => ({
  initialized:state.app.initialized
})
let AppContainer =  connect(mapStateToProps,{initializeApp})(App) ;

const SamuraiJSApp = (props) => {
  return <Provider store={store}>
    <React.StrictMode>
      <AppContainer />
    </React.StrictMode>
  </Provider>
}
export default SamuraiJSApp;