import React from "react";
import classes from "./Login.module.css"
import { Field, reduxForm } from "redux-form";
import { Input } from "../common/FormsControls/FormsControls";
import { minLengthCreator, required } from "../../utils/validators/validators";
import { connect } from "react-redux";
import { login } from "../../redux/auth-reducer";
import { Navigate } from "react-router-dom";
const LoginForm = ({handleSubmit,error}) => {   
    const minLenth3 = minLengthCreator(3)
    return (
        <form onSubmit={handleSubmit}>
        <div  >
            <Field className={classes.login} placeholder={'Login'} validate ={[required]} component={Input} name={'email'}/>
        </div>
        <div  >
            <Field className={classes.pass} type={'password'} validate ={[required,minLenth3]} placeholder={'Password'} component={Input} name={'password'}/>
        </div>
        <div  >
            <Field className={classes.check} type={'checkbox'} component={Input} name={'rememberMe'}/> Remember me
        </div>
        {error && <div className={classes.formSummaryError}>
            {error}
        </div>}
        <div  >
            <button className={classes.btn}>Log in</button>
        </div>
        <div  >
            <a href="https://social-network.samuraijs.com/signUp" target="_blank" rel="noreferrer">Sign up</a>
        </div>
    </form>
    )
}
const LoginReduxForm = reduxForm({form: 'Login'})(LoginForm)
const Login = (props) => {
    const onSubmit = (formData) => {
        props.login(formData.email,formData.password,formData.rememberMe)
    }
    if (props.isAuth) {
        return <Navigate to="/profile" />
    }
    return (
        <div className={classes.form}>
            <h1>Login</h1>
            <LoginReduxForm onSubmit={onSubmit}/>
        </div>
    );
}
const mapStateToProps = (state) => ({
    isAuth:state.auth.isAuth
})
export default connect(mapStateToProps,{login})(Login);
