import Preloader from '../../common/Preloader/Preloader';
import classes from './ProfileInfo.module.css'
import aki from '../../../assets/images/aki.jpg'
import ProfileStatusWithHooks from './ProfileStatusWithHooks';

const ProfileInfo = ({profile,updateStatus,status}) =>{
    if (!profile){
      return<Preloader/>
    }
    return (
    
    <div>
        {/* <div>
          <img
            className={classes.profileBg}
            src="https://i.ytimg.com/vi/SM_nCWA3CJo/maxresdefault.jpg?sqp=-oaymwEmCIAKENAF8quKqQMa8AEB-AH-CYAC0AWKAgwIABABGGUgTyhKMA8=&rs=AOn4CLBReqi95YxUYVMLQZO4x6I8MV4zYQ"
            alt="img"
          />
        </div> */}
        <div className={classes.profileDesc}>
          <img
            src={profile.photos.large !=null ? profile.photos.large:aki}  
            alt="dwd"
            className={classes.ava}
          />
          
          <p  className={classes.name}>{profile.fullName}</p>
          <ProfileStatusWithHooks status={status} updateStatus={updateStatus}/>
          <p>Обо мне: {profile.aboutMe}</p>
          {profile.lookingForAJob ? <p className={classes.find}>Семейное пложение: В активном поиске</p>
                            :<p className={classes.unfind}>Семейное пложение: Все сложно</p>}

        </div>
    </div>
    );
}
export default ProfileInfo;