import classes from './Post.module.css'
const Post=(props)=>{
    return (
        <div className={classes.item}>
            <img src='https://www.pngall.com/wp-content/uploads/12/Avatar-Profile-PNG.png' alt='img' />
            {props.message}
            <div>
                <span> Likes: </span>{props.likeCount}
            </div>
            <div>
                <button className={classes.like}>Like</button>
            </div>
        </div> 
    );
}

export default Post