import React, { useState } from 'react';
import classes from "./Paginator.module.css"
const Paginator = (props) => {
    let portionSize=10;
    let pagesCount = Math.ceil(props.totalUsersCount / props.pageSize);
    let pages = [];
    for(let i=1; i<=pagesCount;i++) {
        pages.push(i)
    }
    let portionCount = Math.ceil(pagesCount/portionSize);
    let [portionNumber, setPortionNumber] = useState(1);
    let leftPortionPageNumber = (portionNumber-1)*portionSize+1;
    let rightPortionPageNumber = portionNumber * portionSize;
    debugger;
    return(
        
        <div className={classes.pages}>
            {portionNumber > 1 && <button className={classes.buttonPage} onClick={()=> {setPortionNumber(portionNumber - 1)}}>PREV</button>}
            {pages.filter(p => p >= leftPortionPageNumber && p<=rightPortionPageNumber)
            .map((p) => {
                return <span onClick={() => {props.onPageChanged(p)}} className={props.currentPage === p && classes.selectedPage}>{p}</span>
            })}
             {portionCount > portionNumber && <button className={classes.buttonPage} onClick={()=> {setPortionNumber(portionNumber + 1)}}>NEXT</button>}
        </div>
    )
}
export default Paginator;